import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import web3 from './web3';
import lottery from './lottery';

class App extends Component {
  state = {
    manager: '',
    balance: '',
    currentAccount: '',
    contract: '',
    message: '',
    items: [],
    hasWinner: false,
    winners: []
  }; //χρησιμοποιούμε state για να αποθηκεύουμε συγκεκριμένες τιμές


  async componentDidMount() {

    window.ethereum.on('accountsChanged', () => {
      // ... να γίνεται reload η σελίδα, δηλ. να καλείται η componentDidMount()
      window.location.reload();
    });

    const accounts = await web3.eth.getAccounts();
    this.setState({ currentAccount: accounts[0] });

    const balance = await web3.eth.getBalance(lottery.options.address);
    this.setState({ balance });

    const manager = await lottery.methods.getManager().call();
    this.setState({ manager });

    const items = await lottery.methods.getItems().call();
    this.setState({ items });

    const hasWinner = await lottery.methods.thereIsWinner().call();
    this.setState({ hasWinner });

    console.log(this.state.hasWinner);
  }

  destroy = async () => {

    this.setState({ message: 'The contract is being destroyed...' });

    await lottery.methods.finalize().send({
      from: this.state.currentAccount,
    });

    this.setState({ message: 'The contract was successfully destroyed! '});
  }

  transferContract = async () => {

    this.setState({ message: 'Transferring contract...' });

    const newOwner = document.getElementById('newOwner').value;
    
    await lottery.methods.transferContract(newOwner).send({
      from: this.state.currentAccount,
    });

    const manager = await lottery.methods.getManager().call();

    this.setState({ manager });

    this.setState({ message: 'The contract was successfully transferred!' });
  }

  amIWinner = async () => {

    const winners = await lottery.methods.getWinners().call();
    this.setState({ winners });

    const items = await lottery.methods.getItems().call();
    this.setState({ items });

    let firstItem = true;

    let itemCount = 0;

    for(var winner of winners.values()){
      if(this.state.currentAccount == winner){
        if(firstItem){
          this.setState({ message: "You won a " + items[itemCount].name});
          firstItem = false;
        } else {
          this.setState({ message: this.state.message + " You won a " + items[itemCount].name});
        }
      }
      
      itemCount += 1;
    }

    console.log(winners);
  };

  declareWinner = async () => {

    this.setState({ message: 'Declaring winners...' });

    await lottery.methods.revealWinners().send({
      from: this.state.currentAccount,
    });

    const winners = await lottery.methods.getWinners().call();
    this.setState({ winners });

    const hasWinner = await lottery.methods.thereIsWinner().call();
    this.setState({ hasWinner });

    console.log(winners);

    this.setState({ message: 'Winners successfully declared!' });

  };

  bid = async (event, itemId) => {

    event.preventDefault();

    this.setState({ message: 'Waiting on transaction success...' });

    await lottery.methods.bid(itemId).send({
        from: this.state.currentAccount,
        value: web3.utils.toWei('0.01', 'ether')
    });

    this.setState({ message: 'Your bid was successful!' });
    
    const balance = await web3.eth.getBalance(lottery.options.address);
    this.setState({ balance });

    const items = await lottery.methods.getItems().call();
    this.setState({ items });
    
  };

  withdraw = async () => {

    this.setState({ message: 'Withdrawing eth...' });

    const accounts = await web3.eth.getAccounts();

    await lottery.methods.withdraw().send({
      from: this.state.currentAccount
    });

    const balance = await web3.eth.getBalance(lottery.options.address);
    this.setState({ balance });

    this.setState({ message: 'Withdraw was successful!' });
  };

  reset = async () => {

    this.setState({ message: 'The lottery is reseting...' });
    
    await lottery.methods.reset().send({
      from: this.state.currentAccount
    });

    const accounts = await web3.eth.getAccounts();
    this.setState({ account: accounts[0] });

    const balance = await web3.eth.getBalance(lottery.options.address);
    this.setState({ balance });

    const manager = await lottery.methods.getManager().call();
    this.setState({ manager });

    const items = await lottery.methods.getItems().call();
    this.setState({ items });

    this.setState({ hasWinner: false });

    this.setState({ message: 'The lottery was successfully reset!' });
    
  };

  render() {
    return (
      <div className='App'>
        <h1 className="text-center">Lottery - Ballot</h1><br></br>
        <div className="row">
          {this.state.items.map(item => (
            <div className="col-sm" key={item.itemId}>
              <div className="card" >
                <img src={require(`./img/${item.name}.png`)} className="card-img-top" alt={item.name} height="230" />
                <div className="card-body">
                  <h5 className="card-title">{item.name}</h5>
                  <div className="row">
                    {this.state.currentAccount == this.state.manager 
                    ? <button className="btn btn-primary btn-lg btn-block" disabled><h4>Bid</h4></button> 
                    : <button className="btn btn-primary btn-lg btn-block" onClick={(event) => this.bid(event, item.itemId)}><h4>Bid</h4></button>}
                    <h4 className="text-center mt-4">Tokens: {item.itemTokens.length}</h4>
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>
      
      <div className="row">
        <div className="m-3 col-sm">
          <label htmlFor="owner"><h4>Contract Manager: {this.state.manager}</h4></label>
          <div id="managerAddress"></div>
        </div>

        <div className="m-3 col-sm">
          <label htmlFor="balance"><h4>Balance: {web3.utils.fromWei(this.state.balance, 'ether')} ETH</h4></label>
        </div>
      </div>

      <div className="row">
        <div className="m-3 col-sm">
          <label htmlFor="current"><h4>Current Address: {this.state.currentAccount}</h4></label>
        </div>

        <div className="m-3 col-sm">
          <h4>{this.state.message}</h4>

          <div className="m-3 col-sm">
          <input htmlFor="newOwner" id="newOwner" size="50" placeholder="Type the new owner's address..."></input>
          </div>
          
          {!(this.state.currentAccount == this.state.manager) && this.state.hasWinner
          ? <button className="btn btn-success m-3" onClick={this.amIWinner}>Am I Winner</button>
          : <button className="btn btn-success m-3" disabled>Am I Winner</button>}

          {this.state.currentAccount == this.state.manager 
          ? <button className="btn btn-success m-3" onClick={this.withdraw}>Withdraw</button>
          : <button className="btn btn-success m-3" disabled>Withdraw</button>
          }

          {this.state.currentAccount == this.state.manager && !this.state.hasWinner
          ? <button className="btn btn-success m-3" onClick={this.declareWinner}>Declare Winner</button>
          : <button className="btn btn-success m-3" disabled>Declare Winner</button>
          }

          {this.state.currentAccount == this.state.manager
          ? <button className="btn btn-success m-3" onClick={this.reset}>Reset</button>
          : <button className="btn btn-success m-3" disabled>Reset</button>
          }

          {this.state.currentAccount == this.state.manager 
          ? <button className="btn btn-success m-3" onClick={this.transferContract}>Change Owner</button>
          : <button className="btn btn-success m-3" disabled>Change Owner</button>
          }

          {this.state.currentAccount == this.state.manager 
          ? <button className="btn btn-success m-3" onClick={this.destroy}>Destroy</button>
          : <button className="btn btn-success m-3" disabled>Destroy</button>
          }

        </div>

      </div>

    </div>
    );
  }
}

export default App;